@keyframes formInfoPhoto {
	0% {
		opacity: 0.8;
		transform: translate(500px, 300px) scale(3) rotate(45deg);
	}

	100% {
		opacity: 1;
		transform: translate(0, 0) scale(1) rotate(0);
	}
}

@keyframes fromStamp {
	0% {
		opacity: 0;
		transform: scale(10);
	}

	100% {
		opacity: 1;
		transform: scale(1);
	}
}

@keyframes fromCheked {
	0% {
		transform: scale(1);
	}

	50% {
		transform: scale(2);
	}

	100% {
		transform: scale(1);
	}
}
